'use server';

import prisma from '@/lib/prisma';
import { revalidatePath } from 'next/cache';
import { writeFile } from 'fs/promises';
import { join } from 'path';

export async function updateTaskStatus(id: string, status: string) {
    try {
        await prisma.task.update({
            where: { id },
            data: { status },
        });
        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to update task status:', error);
        return { success: false, error: 'Failed to update task status' };
    }
}

export async function deleteTask(id: string) {
    try {
        await prisma.task.delete({
            where: { id },
        });
        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to delete task:', error);
        return { success: false, error: 'Failed to delete task' };
    }
}

export async function createTask(data: { title: string; description?: string }) {
    try {
        const user = await prisma.user.findUnique({
            where: { email: 'demo@example.com' }
        });

        console.log('CreatesTask - Found User:', user);

        if (!user) throw new Error('User not found');

        await prisma.task.create({
            data: {
                title: data.title,
                description: data.description,
                status: 'pending',
                userId: user.id,
                emailSource: JSON.stringify({
                    sender: { name: 'Manual Task', email: '' },
                    preview: data.description || 'No description',
                    tags: ['Manual']
                })
            },
        });
        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to create task:', error);
        return { success: false, error: 'Failed to create task' };
    }
}

// Helper to recount items in a folder
async function updateFolderItemCount(folderId: string) {
    if (!folderId) return;
    try {
        const count = await prisma.workspaceFile.count({
            where: { parentId: folderId }
        });
        await prisma.workspaceFile.update({
            where: { id: folderId },
            data: { items: `${count} items` }
        });
    } catch (e) {
        console.error('Failed to update folder count:', e);
    }
}

// Helper to get next order value
async function getNextOrderValue(parentId: string | null) {
    const lastFile = await prisma.workspaceFile.findFirst({
        where: { parentId },
        orderBy: { order: 'desc' }
    });
    return (lastFile?.order || 0) + 1000;
}

export async function createFile(data: { name: string, type: string, size?: string, items?: string, parentId?: string }) {
    try {
        const user = await prisma.user.findUnique({
            where: { email: 'demo@example.com' }
        });
        if (!user) throw new Error('User not found');

        const nextOrder = await getNextOrderValue(data.parentId || null);

        const newFile = await prisma.workspaceFile.create({
            data: {
                name: data.name,
                type: data.type,
                size: data.size,
                items: data.items,
                parentId: data.parentId,
                userId: user.id,
                shared: false,
                order: nextOrder
            }
        });

        if (data.parentId) {
            await updateFolderItemCount(data.parentId);
        }

        revalidatePath('/');
        return { success: true, file: newFile };
    } catch (error) {
        console.error('Failed to create file:', error);
        return { success: false, error: 'Failed to create file' };
    }
}

export async function reorderFiles(items: { id: string, order: number }[]) {
    try {
        // Run in transaction for safety
        await prisma.$transaction(
            items.map((item) =>
                prisma.workspaceFile.update({
                    where: { id: item.id },
                    data: { order: item.order }
                })
            )
        );
        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to reorder files:', error);
        return { success: false, error: 'Failed to reorder files' };
    }
}

export async function deleteFile(id: string) {
    try {
        // Get file first to check parent
        const file = await prisma.workspaceFile.findUnique({ where: { id } });

        await prisma.workspaceFile.delete({
            where: { id }
        });

        if (file?.parentId) {
            await updateFolderItemCount(file.parentId);
        }

        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to delete file:', error);
        return { success: false, error: 'Failed to delete file' };
    }
}

export async function uploadFile(formData: FormData) {
    try {
        const file = formData.get('file') as File;
        const parentId = formData.get('parentId') as string | null;
        if (!file) throw new Error('No file uploaded');

        const bytes = await file.arrayBuffer();
        const buffer = Buffer.from(bytes);

        const fileName = file.name;
        const uploadDir = join(process.cwd(), 'public', 'uploads');
        // mkdir -p public/uploads is already done but let's be safe
        const filePath = join(uploadDir, fileName);
        await writeFile(filePath, buffer);

        const sizeInMb = file.size / (1024 * 1024);
        const sizeStr = sizeInMb < 0.1
            ? `${(file.size / 1024).toFixed(1)} KB`
            : `${sizeInMb.toFixed(1)} MB`;

        const ext = fileName.split('.').pop()?.toLowerCase() || 'file';
        let type = ext;
        if (['jpg', 'jpeg', 'png', 'webp', 'gif'].includes(ext)) type = 'image';
        if (ext === 'pdf') type = 'pdf';

        // Reuse createFile which handles the count update
        return await createFile({
            name: fileName,
            type: type,
            size: sizeStr,
            parentId: parentId || undefined
        });
    } catch (error) {
        console.error('Failed to upload file:', error);
        return { success: false, error: 'Failed to upload file' };
    }
}

export async function moveFile(id: string, parentId: string | null) {
    try {
        const file = await prisma.workspaceFile.findUnique({ where: { id } });
        const oldParentId = file?.parentId;

        await prisma.workspaceFile.update({
            where: { id },
            data: { parentId }
        });

        if (oldParentId) await updateFolderItemCount(oldParentId);
        if (parentId) await updateFolderItemCount(parentId);

        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to move file:', error);
        return { success: false, error: 'Failed to move file' };
    }
}

export async function renameFile(id: string, name: string) {
    try {
        await prisma.workspaceFile.update({
            where: { id },
            data: { name }
        });
        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to rename file:', error);
        return { success: false, error: 'Failed to rename file' };
    }
}

export async function toggleFileShare(id: string) {
    try {
        const file = await prisma.workspaceFile.findUnique({ where: { id } });
        if (!file) throw new Error('File not found');

        await prisma.workspaceFile.update({
            where: { id },
            data: { shared: !file.shared }
        });
        revalidatePath('/');
        return { success: true };
    } catch (error) {
        console.error('Failed to toggle share:', error);
        return { success: false, error: 'Failed to toggle share' };
    }
}
import { parseEmailToTask, EmailPayload } from '@/lib/email-processor';

export async function simulateIncomingEmail(payload: EmailPayload) {
    try {
        const user = await prisma.user.findUnique({
            where: { email: 'demo@example.com' }
        });

        if (!user) throw new Error('User not found');

        // Parse the email "intelligently"
        const parsedData = parseEmailToTask(payload);

        // Create the task
        const newTask = await prisma.task.create({
            data: {
                title: parsedData.title,
                description: parsedData.description,
                status: 'unread', // New emails start as unread
                userId: user.id,
                emailSource: JSON.stringify({
                    sender: {
                        name: payload.from.split('@')[0], // Simple name extraction
                        email: payload.from
                    },
                    preview: parsedData.description.substring(0, 100) + (parsedData.description.length > 100 ? '...' : ''),
                    tags: parsedData.tags,
                    priority: parsedData.priority
                }),
                // We could map priority/tags to Prisma model later if we extend the schema
            }
        });

        revalidatePath('/');
        return { success: true, task: newTask };
    } catch (error) {
        console.error('Failed to process email:', error);
        return { success: false, error: 'Failed to process email' };
    }
}
